@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@700&family=Source+Sans+Pro:wght@300;400&display=swap");

body {
  font-size: 16px;

  margin: 0;
  padding: 0;
  box-sizing: border-box;

  display: flex;
  align-items: center;
  justify-content: center;

  width: 100vw;
  min-height: 100vh;

  text-align: center;

  background: rgb(33, 41, 104);
  background: -moz-linear-gradient(
    90deg,
    rgba(33, 41, 104, 1) 0%,
    rgba(27, 33, 78, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(33, 41, 104, 1) 0%,
    rgba(27, 33, 78, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(33, 41, 104, 1) 0%,
    rgba(27, 33, 78, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#212968",endColorstr="#1b214e",GradientType=1);
}

h1 {
  font-size: 2rem;
  font-family: "Montserrat", sans-serif;
}

p {
  font-size: 1.5rem;
  font-family: "Source Sans Pro", sans-serif;
}

section {
  max-width: 70%;
  margin-inline: auto;
}

.wrapper {
  background-color: #2f3a7e;
  padding: 20px 100px;
  margin: 50px;

  border-radius: 15px;

  box-shadow: 0px 10px 20px rgba(20, 0, 62, 0.2);
}

.wrapper h1 {
  color: white;
}

.btn {
  font-size: 1.5rem;
  color: white;
  border: none;
  padding: 20px 100px;
  border-radius: 15px;

  background: rgb(125, 3, 247);
  background: -moz-linear-gradient(
    90deg,
    rgba(125, 3, 247, 1) 0%,
    rgba(223, 0, 255, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(125, 3, 247, 1) 0%,
    rgba(223, 0, 255, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(125, 3, 247, 1) 0%,
    rgba(223, 0, 255, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#7d03f7",endColorstr="#df00ff",GradientType=1);
}

.btn:hover {
  cursor: pointer;

  background: rgb(92, 0, 185);
  background: -moz-linear-gradient(
    90deg,
    rgba(92, 0, 185, 1) 0%,
    rgba(172, 0, 196, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(92, 0, 185, 1) 0%,
    rgba(172, 0, 196, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(92, 0, 185, 1) 0%,
    rgba(172, 0, 196, 1) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#5c00b9",endColorstr="#ac00c4",GradientType=1);
}

.answer {
  color: white;
}

@media screen and (max-width: 600px) {
  h1 {
    font-size: 1.5rem;
  }
  .wrapper {
    padding: 20px;
  }
  .btn {
    font-size: 1.2rem;
    padding: 20px;
    width: 100%;
  }
  section {
    max-width: 80%;
    margin-inline: auto;
  }
}
